.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.skill-img{
  height: 150px;
}

.linko{
  cursor: pointer;
}

.jumbotron{
  padding: 120px 30px 70px 30px !important;
  margin: 0px auto !important;
  background-color: #0FAAEE !important;
  color:white;
}

.card-img-overlay{
  color: white;
  align-self: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.footer{
  background-color: #BFF0F5;
  margin: 0px auto;
  padding: 20px 0px 20px 0px;
}

.navbar-dark{
  background-color: #0A5FA9;
}

.App-link {
  color: #61dafb;
}

.row-content{
  border-bottom: 1px ridge;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
